<template>
    <v-container fluid>    
        <div class="container">
            <v-form v-model="valid" class="container__form" ref="form">
                <h3 class="container__form__title">Personal</h3>            
                <v-row dense>
                    <v-col cols="12" sm="8">
                        <v-text-field
                        v-model="userData.name"
                        :rules="[rules.required]"
                        label="Name"
                        solo
                        placeholder="name"  
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model="userData.id"
                        :rules="[rules.required]"
                        label="id"
                        solo
                        type="number"
                        placeholder="id"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model="userData.username"
                        :rules="[rules.required]"
                        label="username"
                        solo
                        placeholder="username"
                        ></v-text-field>                            
                    </v-col>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model="userData.email"
                        type="email"
                        :rules="[rules.required, rules.email]"
                        label="E-mail"
                        solo
                        placeholder="e-mail"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <h3 class="container__form__title">Address</h3>
                <v-row dense>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model="userData.address.street"
                        :rules="[rules.required]"
                        label="Street"
                        solo
                        placeholder="street"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model="userData.address.suite"
                        :rules="[rules.required]"
                        label="Suite"
                        solo
                        placeholder="suite"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4">
                        <v-text-field
                        v-model="userData.address.city"
                        :rules="[rules.required]"
                        label="City"
                        solo
                        placeholder="city"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col>
                        <v-text-field
                        v-model="userData.address.geo.lat"
                        :rules="[rules.required]"
                        label="latitude"
                        solo
                        placeholder="latitude"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                        <v-text-field
                        v-model="userData.address.geo.lng"
                        :rules="[rules.required]"
                        label="longitude"
                        solo
                        placeholder="longitude"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row dense>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model="userData.phone"
                        :rules="[rules.required]"
                        label="Phone"
                        solo
                        placeholder="phone"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6">
                        <v-text-field
                        v-model="userData.website"
                        :rules="[rules.required]"
                        label="Website"
                        solo
                        placeholder="website"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <h3 class="container__form__title">Company</h3>
                <v-row dense>
                    <v-col cols="12">
                        <v-text-field
                        v-model="userData.company.name"
                        :rules="[rules.required]"
                        label="Company Name"
                        solo
                        placeholder="company name"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        v-model="userData.company.catchPhrase"
                        :rules="[rules.required]"
                        label="Company Catchphrase"
                        solo
                        placeholder="company catchphrase"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        v-model="userData.company.bs"
                        :rules="[rules.required]"
                        label="Company Bs"
                        solo
                        placeholder="company bs"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-btn
                :disabled="!valid"
                @click="handleAddUserClick"
                class="container__form__button"
                placeholder="company bs"
                >
                    Add User
                </v-btn>
            </v-form>
            <v-dialog v-model="showPopup" class="container__modal" width="200" origin="top left">
                <v-card class="container__modal__card" height="100">
                    <v-card-title class="container__modal__card__title">SUCCESS</v-card-title>
                    <v-btn
                    text                
                    icon
                    class="container__modal__card__button"
                    @click="showPopup = false"
                    color="#fff"
                    >
                        <v-icon class="container__modal__card__button__icon">mdi-close</v-icon>
                    </v-btn>
                </v-card>
            </v-dialog>
        </div>
    </v-container>
</template>

<script setup lang="ts">
import { ref } from 'vue'
import axios from 'axios'

interface UserData {
    id: number | null,
    name: string,
    username: string,
    email: string,
    address : {
        street: string,
        suite: string,
        city: string,
        zipcode: string,
        geo : {
            lat: string,
            lng: string,
        }
    },
    phone: string,
    website: string,
    company: {
        name: string,
        catchPhrase: string,
        bs: string,
    }
}

const valid = ref<(boolean)>(false);
const form = ref<HTMLFormElement | null>(null);
const showPopup = ref<boolean>(false)
const userData = ref<UserData>({
    id: null,
    name: '',
    username: '',
    email: '',
    address : {
        street: '',
        suite: '',
        city: '',
        zipcode: '',
        geo: {
            lat: '',
            lng: '',
        }
    },
    phone: '',
    website: '',
    company: {
        name: '',
        catchPhrase: '',
        bs: '',
    }
});

const rules = ref({
    required: (value:string | number) => !!value || 'Required',
    email: (value: string) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        
        return pattern.test(value) || 'Invalid e-mail'
    }
})

const handleAddUserClick = async () => {
    await axios({
        method: 'POST',
        url: 'https://jsonplaceholder.typicode.com/users',
        data: userData.value
    })
    showPopup.value = true;
    const modalTimer = setTimeout(() => {
        showPopup.value = false;
        clearTimeout(modalTimer);
    }, 5000)
    form.value?.reset()
}
</script>

